/* tslint:disable:component-selector-name  component-selector-type */
import { Component, Input, Output, HostBinding, EventEmitter } from '@angular/core';
import { ColumnComponent } from './column.component';
import { DetailTemplateDirective } from './detail-template.directive';
import { SortDescriptor } from './sort-descriptor';
import { SortSettings, normalize } from './sort-settings';
import { isPresent, isTruthy, isNullOrEmptyString } from './utils';

/**
 * @hidden
 */
const cellContent = `
    <template
        [templateContext]="{
            templateRef: column.headerTemplateRef,
            columnIndex: columnIndex,
            column: column,
            $implicit: column
        }">
    </template>
    <template [ngIf]="!column.headerTemplateRef">{{column.title || column.field}}</template>
`;

/**
 * @hidden
 */
@Component({
    selector: '[kendoGridHeader]',
    template: `
    <template [ngIf]="true">
        <tr>
            <th
                [class.k-hierarchy-cell]="true"
                [class.k-header]="true"
                *ngIf="detailTemplate?.templateRef">
            </th>
            <th
                class="k-header"
                [ngClass]="column.headerClass"
                [ngStyle]="column.headerStyle"
                *ngFor="let column of columns; let columnIndex = index">
                <template [ngIf]="!isSortable(column)">${cellContent}</template>
                <template [ngIf]="isSortable(column)">
                    <a href="#" class="k-link" (click)="sortColumn(column)">
                        ${cellContent}
                        <span [ngClass]="sortIcon(column.field)"></span>
                    </a>
                </template>
            </th>
        </tr>
    </template>
    `
})
export class HeaderComponent {
    @Input() public columns: Array<ColumnComponent> = [];
    @Input() public detailTemplate: DetailTemplateDirective;
    @Input() public scrollable: boolean;
    @Input() public sort: Array<SortDescriptor> = new Array<SortDescriptor>();
    @Input() public sortable: SortSettings = false;

    @Output() public sortChange: EventEmitter<Array<SortDescriptor>> = new EventEmitter<Array<SortDescriptor>>();

    @HostBinding('class.k-grid-header') get headerClass(): boolean {
        return !this.scrollable;
    }

    public sortColumn(column: ColumnComponent): boolean {
        this.sortChange.emit(this.toggleSort(column));

        //prevent default
        return false;
    }

    public sortIcon(field: string): any {
        const state = this.sortDescriptor(field);

        return {
            'k-icon': isPresent(state.dir),
            'k-i-arrow-s': state.dir === "desc",
            'k-i-arrow-n': state.dir === "asc"
        };
    }

    public toggleSort(column: ColumnComponent): Array<SortDescriptor> {
        const { allowUnsort, mode } = normalize(this.sortable, column.sortable);
        const descriptor = this.toggleDirection(column.field, allowUnsort);

        if (mode === 'single') {
            return [descriptor];
        }

        return [...this.sort.filter(desc => desc.field !== column.field), descriptor];
    }

    protected isSortable(column: ColumnComponent): boolean {
        return !isNullOrEmptyString(column.field)
            && isTruthy(this.sortable) && isTruthy(column.sortable);
    }

    protected toggleDirection(field: string, allowUnsort: boolean): SortDescriptor {
        const descriptor = this.sortDescriptor(field);
        let dir = 'asc';

        if (descriptor.dir === 'asc') {
            dir = 'desc';
        } else if (descriptor.dir === 'desc' && allowUnsort) {
            dir = undefined;
        }

        return <SortDescriptor>{ dir, field };
    }

    private sortDescriptor(field: string): SortDescriptor {
        return this.sort.find(item => item.field === field) || <SortDescriptor>{ field };
    }
}



